
.api-title {
  margin-top: 20px;
  font-family: x-locale-heading-primary,zillaslab,Palatino,"Palatino Linotype",x-locale-heading-secondary,serif;
  font-size: 32px;
}
.api-tip {
  font-size: 16px;
  color: #00a1d6;
  padding: 2px 8px;
  border-radius: 4px;
  background: #eee;
  vertical-align: 4px;
}
.api-usage {
  font-size: 16px;
  margin: 20px 0;
}
.api-grammar {
  font-size: 16px;
  background: #e4f0f5;
  padding: 5px 0 5px 20px;
  margin: 20px 0;
}
.api-param-usage {
  padding:0 20px;
  margin: 10px 0;
  font-size: 16px;
}
.api-param-type {
  color: #00a1d6;
  padding: 2px 8px;
  border-radius: 4px;
  background: #eee;
}
.api-param-error {
  font-size: 16px;
  background: #FFEFD5;
  padding: 5px 0 5px 20px;
  margin: 20px 0;
  color: #FF3030;
}
#api input, #api select {
  border: 1px solid #aaa;
  color: #2c3e50;
  outline: none;
  border-radius: 3px;
  -webkit-transition: border-color 0.2s ease;
  transition: border-color 0.2s ease;
}
.api-main {
  width: 1000px;
  margin: 0 auto;
}
.api-quotation, .api-point {
  font-size: 32px;
}
.api-input {
  padding: 0 10px;
  font-size: 16px;
}
.api-output {
  font-size: 24px;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: courier new, courier, monospace;
}
.api-methods {
  font-size: 16px;
  padding: 0 5px;
}
.api-param {
  text-align: center;
  font-size: 16px;
  width: 50px;
}
.api-sign {
  font-size: 32px;
}
.api-code {
  margin: 20px 0;
  font-size: 16px;
  font-family: Consolas, Monaco, 'Andale Mono', monospace;
  white-space: pre-wrap;
  word-wrap: break-word;
}
@media screen and (max-width: 1000px) {
.api-main{
    width: auto;
}
.api-title {
    font-size: 24px;
    text-align: center;
}
.api-tip {
    font-size: 16px;
}
.api-usage {
    font-size: 14px;
    padding: 20px;
    margin: 0;
}
.api-grammar {
    font-size: 16px;
    padding: 10px 0;
    margin: 0;
    text-align: center;
}
.api-param-usage {
    font-size: 14px;
}
.api-param-error {
    font-size: 14px;
}
}



