
.gomoku {
    text-align: center;
    background: #f3dbad;
    position: relative;
    width: 100%;
/*     height: 800px; */
    z-index: 0;
    overflow: hidden;
}
.gomoku-bg {
    background-image: url('../assets/bg/gomoku.jpg');
    width: 1024px;
    height: 608px;
    position: absolute;
    right: 50%;
    bottom: 50%;
    -webkit-transform: translate(50%,50%);
            transform: translate(50%,50%);
    background-size: cover;
    opacity: 0.3;
    z-index: -1;
}
.gomoku-title {
    font-size: 24px;
    padding: 80px 20px 60px;
}
@media screen and (max-width: 1024px) {
.gomoku-bg {
      width: 100vw;
      height: 50vw;
}
}
.gomoku i {
    margin-top: 15px;
    width: 80px;
    height: 80px;
}
.icon-single { background: url('../assets/icon/single.png')
}
.icon-single-hover { background: url('../assets/icon/single-hover.png')
}
.icon-double { background: url('../assets/icon/double.png')
}
.icon-double-hover { background: url('../assets/icon/double-hover.png')
}
.icon-ai { background: url('../assets/icon/ai.png')
}
.icon-ai-hover { background: url('../assets/icon/ai-hover.png')
}
.gomoku-nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
}
.gomoku-nav-item {
    display: inline-block;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    height: 150px;
    cursor: pointer;
}
.gomoku-nav-item span {
    font-size: 16px;
    line-height: 30px;
}
.gomoku-bar {
    position: absolute;
    width: 33.333%;
    height: 5px;
    background: rgb(18, 150, 219);
    bottom: -5px;
    left: 0px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
.gomoku-cont {
    margin-top: 5px;
    background: #f7f8fa;
    padding: 40px;
    font-size: 14px;
    line-height: 26px;
}
.gomoku-more {
    display: inline-block;
    height: 40px;
    margin-top: 20px;
    line-height: 40px;
    padding: 0 55px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    border: 1px solid rgb(18, 150, 219);
    -webkit-transition: all .3s;
    transition: all .3s;
    color: rgb(18, 150, 219)
}
.gomoku-more:hover {
    color: #fff;
    background: rgb(18, 150, 219);
} 



