
.carousel {
		height: 400px;
		position: relative;
		background: #1e2432;
		color: #fff;
}
.caro-item {
		width: 1200px;
		margin: 0 auto;
		height: 400px;
}
.caro-icon {
		position: absolute;
		width: 60px;
		height: 60px;
		background-position: center;
		background-repeat: no-repeat;
		top: 170px;
		cursor: pointer;
}
.caro-icon:hover {
		border-radius: 30px;
		background-color: rgba(0, 0, 0,0.2);
}
.caro-icon-left {
		left: 20px;
}
.caro-icon-right {
		right: 20px;
}
.icon-caro-left { background-image: url('../assets/icon/caro-left.png')
}
.icon-caro-right { background-image: url('../assets/icon/caro-right.png')
}
.caro-bar {
		position: absolute;
		bottom: 10px;
		left: 50%;
		-webkit-transform : translateX(-50%);
		        transform : translateX(-50%)
}
.caro-bar-item {
		display: inline-block;
		width: 30px;
		height: 23px;
		margin: 0 4px;
		position: relative;
		cursor: pointer;
}
.caro-bar-item-div {
		height: 3px;
		width: 30px;
		position: absolute;
		background: #656c7c;
		top: 10px;
}
.caro-bar-item-active {
		background: #fff;
}
@media screen and (max-width: 1000px) {
.caro-item {
			width: 100%;
}
}
@media screen and (max-width: 800px) {
.caro-item {
			width: 100%;
}
.caro-icon {
			display: none;
}
.caro-bar-item {
			width: 10px;
			height: 10px;
}
.caro-bar-item-div {
			width: 6px;
			height: 6px;
			border-radius: 3px;
			top: 2px;
			left: 2px;
}
}



