
.animes {
  text-align: center;
  padding-top: 80px;
}
.animes-title {
  font-size: 24px;
  padding: 0 20px 20px;
}
.animes-info {
  font-size:16px;
  padding: 0 20px 40px;
}
.animes-search {
  height: 40px;
  padding-bottom: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.animes-input-keyword {
  width: 400px;
  height: 38px;
  padding: 0 10px;
  -webkit-box-shadow: none;
          box-shadow: none;
  font-size: 14px;
  border-radius: 4px;
  border: 1px solid #aaa;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.animes-input-keyword:focus {
  border-color: #00a1d6;
}
.animes-input-search {
  height: 40px;
  padding: 0 20px;
  border: none;
  background: #00a1d6;
  color: #fff;
  font-size: 18px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 4px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.animes-input-search:hover {
  background: #29aad4;
}
.animes-box-container {
  background: #373d41;
}
.animes-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1500px;
  margin: 0 auto;
}
.animes-item {
  width: 300px;
  height: 420px;
  overflow: hidden;
  position: relative;
}
.animes-item-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #161a1d;
  opacity: 0.6;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.animes-item-title {
  position: absolute;
  top: 48%;
  color: #fff;
  font-size: 24px;
  text-align: center;
  width: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.animes-item-title-hover {
  top: 20%;
}
.animes-item-bg-hover {
  background: #33cde5;
  opacity: 0.95;
}
.animes-item-info {
  position: absolute;
  top: 80%;
  color: #fff;
  font-size: 15px;
  text-align: center;
  width: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0;
}
.animes-item-info-hover {
  top: 45%;
  opacity: 1;
}
.animes-item-more {
  position: absolute;
  top: 100%;
  color: #fff;
  font-size: 15px;
  text-align: center;
  width: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0;
}
.animes-item-more {
  position: absolute;
  top: 100%;
  color: #fff;
  font-size: 15px;
  text-align: center;
  width: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0;
}
.animes-item-more-hover {
  top: 80%;
  opacity: 1;
}
.animes-item-more a {
  border: 1px solid #fff;
  padding: 10px 20px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  color: #fff;
}
.animes-item-more a:hover {
  background: #fff;
  color: #33cde5;
}
.animes-image {
  width: 100%;
  height: 100%;
  background-size: cover;
}
.animes-image-1463371 {background-image: url('../assets/animes/1463371.jpg');
}
.animes-image-1457573 {background-image: url('../assets/animes/1457573.jpg');
}
.animes-image-5397537 {background-image: url('../assets/animes/5397537.jpg');
}
.animes-image-2043155 {background-image: url('../assets/animes/2043155.jpg');
}
.animes-image-5349275 {background-image: url('../assets/animes/5349275.jpg');
}
@media screen and (max-width: 1500px) {
.animes-box {
    width: 100%;
}
.animes-item {
    width: 20vw;
    height: 28vw;
}
}
@media screen and (max-width: 1000px) {
.animes-box {
    width: 100%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.animes-item {
    width: 48vw;
    height: 69vw;
}
.animes-small-item {
    display: none;
}
.animes-item-title {
    font-size: 20px;
}
.animes-item-info {
    font-size: 12px;
}
}




