
.sect {
		background: #f7f8fa;
}
.sect-container {
		width: 1200px;
		margin: 0 auto;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
}
.sect-item {
		border-right: 1px solid #cdd;
		-webkit-box-flex: 1;
		    -ms-flex: 1;
		        flex: 1;
    position: relative;
    padding-bottom: 20px;
}
.sect-icon {
    position: absolute;
		width: 32px;
		height: 32px;
    top: 50%;
    -webkit-transform: translate(32px,-50%);
            transform: translate(32px,-50%)
}
.icon-github { background: url('../assets/icon/github.png') no-repeat
}
.icon-user { background: url('../assets/icon/user.png') no-repeat
}
.icon-responsive { background: url('../assets/icon/responsive.png') no-repeat
}
.icon-concise { background: url('../assets/icon/concise.png') no-repeat
}
.sect-item-title {
    margin-left: 82px;
    margin-top: 16px;
    margin-bottom: 5px;
    font-size: 16px;
}
.sect-item-p {
    margin-left: 82px;
    padding-right: 6px;
}
@media screen and (max-width: 1200px) {
.sect-container {
      width: 100%;
}
}
@media screen and (max-width: 800px) {
.sect-container {
      width: 100%;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
.sect-item {
      border-bottom: 1px solid #cdd;
}
}



